.header{
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-top: 30px;
    margin-bottom: 30px;

    /* background-color: chocolate; */ 
}

.header__iconMenu{
    display: scroll;
    position: fixed;
    top: 15px;
    right: 15px;
    font-size: 25px;
    cursor: pointer;
    color: rgb(255, 255, 255);
    /* z-index: 10; */
}
.btn-icon2{
    color: white;
}
.header__iconMenu:hover{
    /* background-color: black; */
    color: rgb(9, 150, 112);
}
#btn-menu{
    display: none;
}

#btn-menu:checked ~ .header__menu{
    opacity: 1;
    visibility: visible;
}

.header__developer{
    font-size: 2rem;
    font-family: 'Dancing Script', cursive;
    /* color: rgb(255, 255, 255); */
}

.header__menu{
    /* margin-top: 10px; */
    position: fixed;
    background-color: rgb(32, 27, 27);
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    transition: all 500ms ease;
    

    display: flex;
    justify-content: flex-end;

    opacity: 0;
    visibility: hidden;
}
.header__menu__container{
    width: 100%;
    max-width: 250px;

    /* background-color: rgb(122, 122, 190); */

    margin-top: 50px;
    margin-right: 30px;

    display: flex;
    align-items: flex-end;
    flex-direction: column;

    
}

.header__menu__container--link{
    text-decoration: none;
    margin: 15px 10px;
    padding: 0 15px;

    color: white;
    font-size: 1.5rem;
    font-weight: bold;

    /* position: relative; */
    transition: all 100ms ease;

    /* display: none; */
}
.header__menu__container--link:hover{
    border-right: 3px solid white;
    /* background-color: rgb(41, 37, 37); */
}
.header__menu__container--link:active{
    color: rgb(9, 150, 112);
    border-right: 3px solid rgb(9, 150, 112);
}
.link-activated{
    color: rgb(255, 255, 255);
    border-right: 3px solid rgb(9, 150, 112);
}

.ShowOffMenu{
    opacity: 0;
    visibility: hidden;
}



@media (min-width: 700px) {
    .header__iconMenu{
        display: none;
    }

    .header__menu{
        position: relative;
        background-color: transparent;
        width: 100%;
        height: auto;
        top: auto;
        left: auto;
        transition: none;
        /* background-color: tomato; */
        display: flex;
        justify-content: center;
        align-items:center;
        opacity: 1;
        visibility: visible;

       
    }

    .header__menu__container{
        width: 700px;
        max-width:max-content;
        /* background-color: rgb(122, 122, 190); */
        display: block;
        padding-bottom: 14px;
        border-bottom: 1px solid rgb(255, 255, 255);
    }

    .header__menu__container--link{
        text-decoration: none;
        margin: 15px 20px;
        padding: 10px 15px;
    
        color: rgb(255, 255, 255);
        font-size: 1.5rem;
        font-weight: bold;
    
        /* position: relative; */
        transition: none;
    
        /* display: none; */
    }
    .header__menu__container--link:hover{
        border-right: none;
        /* background-color: rgb(41, 37, 37); */
    }
    .header__menu__container--link:active{
        color: rgb(200, 194, 194);
        border-right: none;
    }
    .link-activated{
        color: rgb(172, 166, 166);
        border-right: none;
    }
    
}